import { object, string } from 'yup'

export const commentSchema = object({
    text: string()
        .required({
            name: 'text',
            text: '   '
        })
        .min(4, {
            name: 'text',
            text: '  4 '
        })
})

export const registerSchema = object({
    password: string()
        .required({
            name: 'password',
            text: '   '
        })
        .min(8, {
            name: 'password',
            text: '  8 '
        }),
    email: string()
        .email({ name: 'email', text: 'Email  ' })
        .required({ name: 'email', text: '   ' }),
    name: string().required({
        name: 'name',
        text: '   '
    })
})

export const loginSchema = object({
    password: string()
        .required({
            name: 'password',
            text: '   '
        })
        .min(8, {
            name: 'password',
            text: '  8 '
        }),
    email: string()
        .email({ name: 'email', text: 'Email  ' })
        .required({ name: 'email', text: '   ' })
})
